.about-us {
    background: linear-gradient(to right top, #3b202f, #362233, #312436, #2c2537, #272738, #232a3a, #1f2c3c, #1c2f3c, #18333e, #15373f, #143b3e, #183f3c);
    padding: 200px 0;
}

.about-us__body {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 15px;
}

.clicker {
    color: #ffd700;
    font-weight: bold;
    font-style: italic;
    text-decoration: underline;
}

.about-us__title {
    max-width: 320px;
}

.about-us__title .title {
    font-weight: 700;
    line-height: 42px;
    font-size: 32px;
    color: #fff;
}

.about-us__title .subtitle,
.about-us__title .about {
    margin: 15px 0 0 0;
    color: #c4c4c4;
    font-size: 18px;
}

.about-us__title .about {
    margin: 15px 0 0 0;
}



