.clicker-block {
    padding: 200px 0;
    background-image: linear-gradient(to right top, #051937, #0c1d4b, #201e5d, #3a1a6c, #570a76);
}

.clicker-block__body {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.clicker-block__body h3 {
    font-size: 32px;
    color: #fff;
}

.clicker-block__body .count {
    color: #ffd700;
}

.buttons {
    margin: 30px 0 0 0;
}

.btn {
    width: 100px;
    height: 50px;
    cursor: pointer;
    font-weight: bold;
    font-style: italic;
    border: none;
}

.clickBtn {
    width: 150px;
    margin: 0 0 0 30px;
}