.header {
    background: #00154a;
}

.header__body {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 15px;
}

.header__logo {
    color: #fff;
    font-size: 24px;
    font-style: italic;
    font-weight: 500;
}

.header__nav {
    position: fixed;
    bottom: 15px;
    left: 15px;
    right: 15px;
    text-align: center;
    background: #0a1c4a;
    transform: translate(-200%);
    transition: transform .5s ease-in-out;
    padding: 60px 30px 30px 30px;
    border-radius: 20px;
}

.visible {
    transform: translate(0);
}

.header__nav li:not(:first-child) {
    margin: 15px 0 0 0;
}

.header__nav li {
    width: 100%;
    background: rgba(255, 255, 255, 0.19);
    border-radius: 10px;
}

.header__nav_link {
    color: #fff;
    font-size: 24px;
    font-style: italic;
}

.active {
    color: yellow;
}

.fa-xmark {
    color: #f00;
    font-size: 24px;
    position: absolute;
    top: 15px;
    right: 15px;
    cursor: pointer;
}

.burger {
    background: transparent;
    border: none;
    outline: none;
    width: 30px;
    height: 20px;
    display: block;
    cursor: pointer;
    position: relative;
}

.burger::before,
.burger::after {
    content: '';
    width: 100%;
    height: 2px;
    background: #fff;
    position: absolute;
    left: 0;
    transition: all .5s ease-in-out;
}

.burger::before {
    top: 0;
}

.burger::after {
    bottom: 0;
}

.burger__line {
    width: 100%;
    height: 2px;
    background: #fff;
    position: absolute;
    left: 0;
    opacity: 1;
    transition: opacity .5s ease-in-out;
}

.open .burger__line {
    opacity: 0;
}

.open::before {
    transform: rotate(45deg);
    top: 10px;
}

.open::after {
    transform: rotate(-45deg);
    top: 10px;
}